@font-face {
  font-family: "StagBook";
  font-style: normal;
  font-weight: 300;
  src: local("Stag-Book"), local("Stag-Book"), url("../fonts/Stag-Book.woff") format("woff"), url("../fonts/Stag-Book.woff2") format("woff2");
}

@font-face {
  font-family: "StagBookItalic";
  font-style: italic;
  font-weight: 300;
  src: local("Stag-BookItalic"), local("Stag-BookItalic"), url("../fonts/Stag-BookItalic.woff") format("woff"), url("../fonts/Stag-BookItalic.woff2") format("woff2");
}

@font-face {
  font-family: "MyriadPro";
  font-style: normal;
  font-weight: 400;
  src: local("MyriadPro-Regular"), local("MyriadPro-Regular"), url("../fonts/MyriadPro-Regular.woff") format("woff"), url("../fonts/MyriadPro-Regular.woff2") format("woff2");
}

@font-face {
  font-family: "StagBook";
  font-style: normal;
  font-weight: 500;
  src: local("Stag-Medium"), local("Stag-Medium"), url("../fonts/Stag-Medium.woff") format("woff"), url("../fonts/Stag-Medium.woff2") format("woff2");
}

@font-face {
  font-family: "StagBook";
  font-style: normal;
  font-weight: 600;
  src: local("Stag-Semibold"), local("Stag-Semibold"), url("../fonts/Stag-Semibold.woff") format("woff"), url("../fonts/Stag-Semibold.woff2") format("woff2");
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-checkbox-pen-outline {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-clock-circular-outline {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-comment {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-delete {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-male-user-shadow {
  width: 1em;
  height: 1em;
  fill: #010002;
}

.icon-reply-rew {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-reply {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-star {
  width: 1em;
  height: 1em;
  fill: initial;
}

body {
  background: #FFF;
  min-width: 320px;
  font-family: "StagBook";
}

.is-hidden {
  overflow: hidden;
}

#preloader {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: #404040;
  z-index: 99;
}

#circle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 100;
}

#circle span {
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-style: italic;
  font-family: "StagBook";
  font-size: 80px;
  color: #FF1898;
}

#circle p {
  position: absolute;
  display: inline-block;
  white-space: nowrap;
  top: 120%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: "MyriadPro";
  font-size: 30px;
  color: #ffffff;
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  background-color: #f2f2f2;
}

.wrap {
  padding: 0 80px;
}

@media only screen and (max-width: 767px) {
  .wrap {
    padding: 0 35px;
  }
}

.wrap p {
  font-size: 17px;
  line-height: 1.4;
  color: #454545;
}

.wrap p:not(:last-child) {
  margin-bottom: 35px;
}

@media only screen and (max-width: 767px) {
  .wrap p:not(:last-child) {
    margin-bottom: 20px;
  }
}

.wrap p:last-child {
  margin-bottom: 25px;
}

@media only screen and (max-width: 767px) {
  .wrap p {
    font-size: 14px;
  }
}

.inner {
  max-width: 940px;
  margin: 0 auto;
  background-color: #ffffff;
}

.bg-image {
  position: relative;
  width: 100%;
}

.bg-image:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0) 100%);
  z-index: 2;
}

.bg-image img {
  position: relative;
  max-width: 100%;
  z-index: 1;
}

.title-md {
  font-family: "StagBook";
  font-size: 30px;
  line-height: 1;
  font-weight: 600;
  color: #454545;
}

@media only screen and (max-width: 767px) {
  .title-md {
    font-size: 24px;
  }
}

.icon-star {
  width: 35px;
  height: 35px;
  fill: #ffd800;
}

@media only screen and (max-width: 767px) {
  .icon-star {
    width: 25px;
    height: 25px;
  }
}

.date {
  display: inline-block;
  font-size: 15px;
  line-height: 1.1;
  color: #b6b6b6;
}

.date.date--mod {
  margin-bottom: 15px;
}

@media only screen and (max-width: 767px) {
  .date.date--mod {
    margin-bottom: 10px;
  }
}

.list {
  display: flex;
  flex-wrap: wrap;
}

.item {
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 767px) {
  .item {
    margin-bottom: 5px;
  }
}

.item.item_mod {
  margin-left: 55px;
}

@media only screen and (max-width: 767px) {
  .item.item_mod {
    margin-left: 0;
    margin-bottom: 5px;
    width: 100%;
  }
}

.item.item_margin {
  margin-left: 15px;
}

.item.item_posit:not(:last-child) {
  margin-bottom: 30px;
}

.list.list_border {
  padding-top: 18px;
  padding-bottom: 18px;
  border-top: 2px solid #f2f2f2;
  border-bottom: 2px solid #f2f2f2;
  margin-bottom: 40px;
}

.list.list_flex {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.comment-btn {
  display: flex;
  align-items: center;
  font-family: "StagBook";
  background: none;
  font-size: 15px;
}

.comment-btn:hover .label {
  color: #454545;
}

.comment-btn:hover .icon-male-user-shadow,
.comment-btn:hover .icon-clock-circular-outline,
.comment-btn:hover .icon-comment,
.comment-btn:hover .icon-checkbox-pen-outline,
.comment-btn:hover .icon-delete,
.comment-btn:hover .icon-reply,
.comment-btn:hover .icon-reply-rew {
  fill: #454545;
}

.icon-male-user-shadow,
.icon-clock-circular-outline,
.icon-comment,
.icon-checkbox-pen-outline,
.icon-delete,
.icon-reply,
.icon-reply-rew {
  transition: fill .3s;
  height: 18px;
  width: 18px;
  fill: #b6b6b6;
}

.icon-delete {
  height: 12px;
}

.label {
  display: inline-block;
  font-size: 15px;
  line-height: 1.1;
  color: #b6b6b6;
  margin-left: 5px;
  transition: color .3s;
}

.post-message-container p {
  margin-bottom: 10px !important;
}

.post-byline {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

@media only screen and (max-width: 767px) {
  .post-byline {
    flex-wrap: wrap;
  }
}

@media only screen and (max-width: 767px) {
  .post-byline .author {
    margin-bottom: 5px;
  }
}

.author-link {
  font-family: "StagBook";
  font-size: 20px;
  line-height: 1.2;
  font-weight: 600;
  color: #454545;
  text-decoration: none;
  margin-right: 20px;
}

.post {
  margin-bottom: 40px;
}

.post:last-child {
  margin-bottom: 50px;
}

.load-more {
  display: flex;
  align-items: center;
}

.load-more__line {
  display: block;
  width: 100%;
  height: 2px;
  background-color: #f2f2f2;
}

.load-more__btn {
  box-sizing: border-box;
  padding: 12px !important;
  font-family: "StagBook";
  font-size: 17px;
  font-weight: 500;
  line-height: 1.4;
  padding: 12px 20px;
  color: #b6b6b6;
  background: none;
  border-radius: 5px;
  transition: background-color .3s, color .3s, border-color .3s;
  outline: none;
  white-space: nowrap;
  border: 2px solid #f2f2f2;
}

.load-more__btn:hover {
  color: #454545;
  background-color: #ffd800;
  border: 2px solid transparent;
}

@media only screen and (max-width: 767px) {
  .load-more__btn {
    padding: 8px 15px;
  }
}

.header {
  width: 100%;
}

.main {
  position: relative;
  margin-top: -80px;
  z-index: 3;
  padding-bottom: 155px;
}

@media only screen and (max-width: 767px) {
  .main {
    margin-top: 0;
  }
}

.main__inner {
  padding: 45px 0 25px;
  box-shadow: 0 0 35px rgba(0, 0, 0, 0.2);
}

.main__wrap {
  margin-bottom: 35px;
}

@media only screen and (max-width: 767px) {
  .main__wrap {
    margin-bottom: 20px;
  }
}

.main__row {
  display: flex;
  justify-content: flex-start;
  padding-right: 80px;
  margin-bottom: 35px;
}

@media only screen and (max-width: 767px) {
  .main__row {
    flex-wrap: wrap;
    padding: 0 35px;
    margin-bottom: 0;
  }
}

.main__col-left {
  flex: none;
  max-width: 365px;
  margin-right: 20px;
}

@media only screen and (max-width: 767px) {
  .main__col-left {
    flex: 1 1 365px;
    margin: 0 auto 20px;
  }
}

.main__col-left img {
  border: 5px solid #f2f2f2;
  border-left: none;
  max-width: 100%;
}

@media only screen and (max-width: 767px) {
  .main__col-left img {
    border-left: 5px solid #f2f2f2;
  }
}

@media only screen and (max-width: 767px) {
  .main__col-right {
    width: 100%;
    margin-bottom: 20px;
  }
}

.main__col-right p {
  font-size: 17px;
  line-height: 1.45;
  color: #454545;
}

@media only screen and (max-width: 767px) {
  .main__col-right p {
    font-size: 14px;
  }
}

.title-block {
  position: relative;
  margin-bottom: 15px;
}

.title-block.title-block_mod {
  margin-bottom: 40px;
}

.title-block.title-block_mod .title-block__title {
  border-bottom: none;
}

@media only screen and (max-width: 767px) {
  .title-block {
    margin-bottom: 5px;
  }
}

.title-block .title-block__title {
  margin-bottom: 0;
  padding-bottom: 0;
}

.title-block__icon {
  position: absolute;
  display: inline-block;
  left: -50px;
  top: -5px;
}

@media only screen and (max-width: 767px) {
  .title-block__icon {
    top: -2px;
    left: -30px;
  }
}

.title-block__title {
  margin-bottom: 11px;
  border-bottom: 2px solid #f2f2f2;
  padding-bottom: 4px;
}

@media only screen and (max-width: 767px) {
  .title-block__title {
    margin-bottom: 10px;
  }
}

.comment__contein {
  border-bottom: 2px solid #f2f2f2;
  margin-bottom: 40px;
}

.post-box {
  display: flex;
  align-items: flex-start;
}

@media only screen and (max-width: 767px) {
  .post-box {
    flex-wrap: wrap;
  }
}

.textarea-wrapper {
  width: 100%;
  height: 90px;
  padding: 3px;
  border: 2px solid #f2f2f2;
  border-radius: 5px;
}

.textarea-wrapper textarea {
  width: 100%;
  height: 100%;
  border: none;
  resize: none;
  padding: 10px 15px;
  font-size: 17px;
  color: #454545;
  line-height: 1.4;
}

.avatar {
  min-width: 90px;
  height: 90px;
  padding: 3px;
  border: 2px solid #f2f2f2;
  border-radius: 5px;
  margin-right: 20px;
}

.avatar img {
  border-radius: 5px;
  max-width: 100%;
}

@media only screen and (max-width: 767px) {
  .avatar {
    margin-right: 0;
    margin-bottom: 5px;
  }
}

.avatar_size {
  width: 68px;
  height: 68px;
  padding: 3px;
  border: 2px solid #f2f2f2;
  border-radius: 5px;
  margin-right: 20px;
}

.avatar_size img {
  border-radius: 5px;
  max-width: 100%;
}

@media only screen and (max-width: 767px) {
  .avatar_size {
    margin-right: 0;
    margin-bottom: 5px;
  }
}

.button {
  display: flex;
  justify-content: flex-end;
  margin: 10px 0;
}

.btn {
  font-family: "StagBook";
  font-size: 17px;
  font-weight: 500;
  line-height: 1.4;
  padding: 12px 50px;
  color: #454545;
  background: none;
  background-color: #f1c40f;
  border-radius: 5px;
  transition: background-color .3s;
  outline: none;
}

.btn:hover {
  background-color: #ffd800;
}

@media only screen and (max-width: 767px) {
  .btn {
    padding: 8px 30px;
  }
}

.post-content {
  display: flex;
  margin-bottom: 30px;
}

.post-content:last-child {
  margin-bottom: 0;
}

@media only screen and (max-width: 767px) {
  .post-content {
    flex-wrap: wrap;
  }
}

.reply-coment {
  max-width: 670px;
  margin: 0 0 20px auto;
}

.reply-coment__wrap {
  display: none;
  margin-bottom: 20px;
}

.reply-coment__wrap.is-open {
  display: block;
}

.block {
  display: flex;
}
