//FONTS

@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1),
	url("../fonts/" + $file + ".woff") format("woff"),
	url("../fonts/" + $file + ".woff2") format("woff2") {} }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

//START Montserrat
@font-face {
	@include fontdefault(normal, 300, 'StagBook');
	@include fontpath('Stag-Book', 'Stag-Book', 'Stag-Book'); }

@font-face {
	@include fontdefault(italic, 300, 'StagBookItalic');
	@include fontpath('Stag-BookItalic', 'Stag-BookItalic', 'Stag-BookItalic'); }

@font-face {
	@include fontdefault(normal, 400, 'MyriadPro');
	@include fontpath('MyriadPro-Regular', 'MyriadPro-Regular', 'MyriadPro-Regular'); }

@font-face {
	@include fontdefault(normal, 500, 'StagBook');
	@include fontpath('Stag-Medium', 'Stag-Medium', 'Stag-Medium'); }

@font-face {
	@include fontdefault(normal, 600, 'StagBook');
	@include fontpath('Stag-Semibold', 'Stag-Semibold', 'Stag-Semibold'); }


